<template>
	<view>
		<!-- 轮播图 -->
		<view class="slideShow">
			<swiper :indicator-dots="true" 
					:autoplay="true" 
					:interval="3000" 
					:duration="1000" 
					:circular="true" 
					indicator-color="rgb(255,255,255)"
					indicator-active-color="#ffff0a">
				<block v-for="(item,index) in swipperList" :key="index">
					<swiper-item class="swipperItem">
						<image @click="localcont"
							   :src="item.image" 
							   mode="aspectFill">
						</image>
					</swiper-item>
				</block>
			</swiper>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'swipper',
		data() {
			return {
				swipperList: [{
					image: "../../static/image/clock.png"
				},{
					image: "../../static/image/block.png"
				},{
					image: "../../static/image/clock.png"
				}]
			}
		}
	}
</script>

<style>
	.slideShow {
		width: 100%;
		margin-top: 10px;
		margin-left: 0px;
		border-radius: 10px;
		overflow: hidden;
		background-color: #515151;
	}

	.swipperItem {
		width: 100%;
		height: 300px;
	}
	
	.swipperItem image{
		width: 100%;
		height: 100%;
	}
</style>
